import Head from 'next/head'
import Page from '../layouts/main'

export default () => (
  <div>
    <Head>
      <meta name="viewport" content="width=device-width, initial-scale=1" />
      <meta charSet="utf-8" />
    </Head>
    <Page>
    <p>my page with global styles!</p>
  </Page>
    <style jsx global>{`
      body { 
        background: pink;
        font: 11px menlo;
        color: #000;
      }
    `}</style>
  </div>
)